#adno-osd{
    width: 100%;
    height: calc(100vh - 136px);
}

.osd-buttons-bar{
    width: 100%;
    display: flex;
}

.osd-buttons-bar-hidden{
    display: none;
}

.adno-osd-anno-fullscreen{
    position: absolute;
    z-index: 10000;
    top: 2%;
    left: 2%;
    width: 300px;
    height: 200px;
    background-color: white;
    color: cadetblue;
    border-radius: 2px;
    padding: 10px;
    overflow: auto;
}

.adno-osd-anno-fullscreen-tb-opened{
    position: absolute;
    z-index: 10000;
    top: 8%;
    left: 2%;
    width: 300px;
    height: 200px;
    background-color: white;
    color: cadetblue;
    border-radius: 2px;
    padding: 10px;
    overflow: auto;
}


.adno-timer-osd{
    text-align: center;
}